import cls from './ModuleLists.module.scss';
import { useTable } from 'react-table';
import { isMobile } from 'react-device-detect';
import { useState } from 'react';
import { Button } from '../../common';
import { page_default } from '../../../shared/utility';

export const ModuleLists = props => {
	const { columns = [], list = [], title, onListItemClick = row => {}, className = cls.tableContainer, children, isReport = false } = props;
	const [page, setPage] = useState(1);
	const [selected, setSelected] = useState(undefined);
	const columnsF = isMobile ? columns.slice(1, 4) : columns;
	function Table({ columns, data }) {
		const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } = useTable({
			columns,
			data,
		});
		return (
			<div className={title ? (isReport ? cls.tableReport : cls.tableHost) : cls.tableHostSimple}>
				<table {...getTableProps()} size="sm">
					<thead>
						{headerGroups.map((headerGroup, index) => (
							<tr key={index} {...headerGroup.getHeaderGroupProps()}>
								{headerGroup.headers.map((column, idx) => (
									<th key={idx} {...column.getHeaderProps()}>
										<div style={{ border: '0' }}>{column.render('Header')}</div>
									</th>
								))}
							</tr>
						))}
					</thead>
					<tbody {...getTableBodyProps()}>
						{rows.map(
							(row, index) =>
								prepareRow(row) || (
									<tr
										key={index}
										{...row.getRowProps()}
										onClick={() => {
											setSelected(index);
											onListItemClick(title ? (page == 12345 ? list[row.index] : list.slice(page * page_default - page_default, page * page_default)[row.index]) : list[row.index]);
										}}
									>
										{row.cells.map((cell, index) => {
											return (
												<td key={index} {...cell.getCellProps()}>
													<div style={{ border: '0' }}>{cell.render('Cell')}</div>
												</td>
											);
										})}
									</tr>
								)
						)}
					</tbody>
				</table>
			</div>
		);
	}

	return (
		<div className={className ?? ''} app-variant-is-mobile={isMobile ? 'true' : 'false'} style={{ display: 'flex', flexDirection: 'column' }}>
			<div style={{ marginBottom: '10px' }}>
				{children}
				{!title && (
					<Table
						columns={columnsF.map(x => {
							return {
								Header: () => {
									return <span>{x}</span>;
								},
								accessor: x,
							};
						})}
						data={list}
					/>
				)}

				{title && (
					<Table
						columns={[
							{
								Header: title,
								columns: columnsF.map(col => {
									return {
										Header: col,
										accessor: col,
									};
								}),
							},
						]}
						data={page == 12345 ? list : list.slice(page * page_default - page_default, page * page_default)}
					/>
				)}

				{title && list.length > 0 && list.length > page_default + 1 && list.slice(page * page_default - page_default, page * page_default).length < page_default + 1 && (
					<div style={{ display: 'flex' }}>
						<p style={{ padding: '10px 10px 0 0', fontSize: '12px' }}>Page:</p>
						{[...Array(Math.ceil(list.length / page_default))].map((obj, index) => (
							<Button disabled={index == page - 1 ? true : false} title={index + 1} clear key={index} style={{ width: '10px', display: index < 18 ? '' : 'none' }} clicked={() => setPage(index + 1)} />
						))}
						<Button title={'All'} disabled={page == 12345 ? true : false} clicked={() => setPage(12345)} clear />
					</div>
				)}
			</div>
		</div>
	);
};

export default ModuleLists;
