import GoogleMapReact from 'google-map-react';
import classes from './MapPin.module.scss';
import { useState, useRef } from 'react';
const R = require('ramda');

const MapPin = props => {
	const { onLocationChange, value } = props;
	// const latRef = useRef(121.012255);
	// const lngRef = useRef(14.652116);
	const latRef = useRef(150.9171779);
	const lngRef = useRef(-33.8149597);
	const location = value ? R.split(',', value) : [14.652116, 121.012255];

	const [points, setPoints] = useState({ id: 1, title: 'Current Location', lat: +location[0], lng: +location[1] });
	const MyMarker = ({ text, tooltip }) => (
		<div className={classes.circle}>
			<span className={classes.circleText} title={tooltip}>
				{text}
			</span>
		</div>
	);
	const distanceToMouse = (pt, mp) => {
		if (pt && mp) {
			return Math.sqrt((pt.x - mp.x) * (pt.x - mp.x) + (pt.y - mp.y) * (pt.y - mp.y));
		}
	};

	return (
		<div style={{ height: '600px', width: '100%' }}>
			Pin location
			<div>(click mouse to change location or enter location values in textbox below)</div>
			<div style={{ height: '80%', width: '100%' }}>
				<GoogleMapReact
					onClick={ev => {
						setPoints({
							id: 1,
							title: 'My pin',
							lat: ev.lat,
							lng: ev.lng,
						});
						onLocationChange({
							lat: ev.lat,
							lng: ev.lng,
						});
					}}
					bootstrapURLKeys={{
						key: 'AIzaSyDEZgyux-heiGo2ur369715z1-CtG1S4PI',
						language: 'en',
						region: 'US',
					}}
					center={{ lat: +points.lat, lng: +points.lng }}
					defaultZoom={15}
					distanceToMouse={distanceToMouse}
				>
					{value != undefined && <MyMarker key={points.id} lat={points.lat} lng={points.lng} tooltip={points.title} />}
				</GoogleMapReact>
			</div>
			<br />
			<span>
				Longitude:
				<input
					type="number"
					ref={lngRef}
					name="input-longitude"
					className={classes.inputField}
					value={points.lng}
					onChange={e => {
						lngRef.current.value = e.target.value;
						setPoints({
							id: 1,
							title: 'My pin',
							lat: latRef.current.value,
							lng: lngRef.current.value,
						});
					}}
				/>
				Latitude:
				<input
					type="number"
					ref={latRef}
					name="input-latitude"
					className={classes.inputField}
					value={points.lat}
					onChange={e => {
						latRef.current.value = e.target.value;
						setPoints({
							id: 1,
							title: 'My pin',
							lat: latRef.current.value,
							lng: lngRef.current.value,
						});
					}}
				/>
			</span>
		</div>
	);
};

export default MapPin;
